import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  helper: {
    borderBottom: 'none',
  }
});

export default function SpecTable({label, rows}) {
  const clz = useStyles();
  const noBorderBottom = {classes: {root: clz.helper}};

  return (
    <TableContainer component="div">
      <Table className={clz.table} size="small" aria-label={label}>
        <TableHead>
          <TableRow>
            <TableCell {...noBorderBottom}>{label}</TableCell>
            <TableCell {...noBorderBottom}></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell {...noBorderBottom} component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell {...noBorderBottom}>{row.value}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}